.main {
  margin: 2.5% 0;
  padding: 5% 0;
  display: grid;
  grid-template-columns: 30% 30% 30%;
  justify-content: space-around;
  gap: 2.5%;

}
.icon {
  font-size: 3rem;
  cursor: pointer;
}
.main__backend,
.main__frontend,
.main__additional {
  padding: 0 15px 10px;
  border-radius: 10px;
  box-shadow: var(--shadow);
}
.main__frontend--icon,
.main__backend--icon,
.main__additional--icon {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 2%;
}

.main__frontend--header,
.main__backend--header,
.main__additional--header {
  padding: 10%;
}

.icon__wrapper {
  padding: 10px;
}

@media screen and (max-width: 991px) {
  .main {
    grid-template-columns: 40% 40%;
  }
}

@media screen and (max-width: 768px) {
  .main {
    grid-template-columns: 90%;
  }
}
