.contact__wrapppr {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: fixed;
  bottom: 5%;
  right: 2.5%;
  gap: 10px;
  z-index: 100;
}
.contact {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding: 10px;
  gap: 10px;
  backdrop-filter: blur(30px);
  border: 0.1px solid #fff;
  border-radius: 5px;
}
.contact__email > input {
  width: 250px;
  padding: 10px;
  border: none;
  outline: none;
  box-shadow: 0px 0px 5px 2px #0088cc;
  margin: 5px 0;
}
.contact__email > input:invalid {
  box-shadow: 0px 0px 5px 2px red;
}
.contact__email > input:valid {
  box-shadow: 0px 0px 5px 1px green;
}
.contact__desc > textarea {
  resize: none;
  width: 250px;
  border: none;
  outline: none;
  padding: 5px;
  padding-right: 50px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.contact__desc > textarea::placeholder {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.contact__send {
  position: absolute;
  bottom: 20px;
  left: 222px;
  cursor: pointer;
  z-index: 0;
}
.contact__icon--wrapper {
  line-height: 1rem;
  position: relative;
  padding: 10px;
  border-radius: 20px;
  box-shadow: var(--shadow);
  backdrop-filter: blur(10px);
}

.contact_anime_show {
  animation: show 1s 1 ease-in-out alternate;
}
.contact_anime_hide {
  transform: translate(40%, 60%) scale(0);
  transition: 0.5s all;
}
@keyframes show {
  0% {
    transform: translate(40%, 60%) scale(0);
  }
  30% {
    transform: translate(0) scale(1.2);
  }
  70% {
    transform: translateX(0) scale(1);
  }
}
