.about,
.about__blog {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-block: 2rem;
}
.about {
  margin: 5%;
  border-radius: 0 !important;
  float: none !important;
  width: 50%;
  transform: translateX(40%);
  position: relative;
}
.about__text-primary {
  font-size: 2rem;
  margin: 10px auto;
}
.about__text-secondary {
  font-size: 1.5rem;
  color: #00000090;
  margin: 10px auto;
}
.about__blog-image {
  box-shadow: var(--shadow);
  width: 200px;
  min-height: 200px;
  object-fit: cover;
  margin: auto;
  border-radius: 50%;
}
.about__info {
  border-radius: 50%;
  position: absolute;
  bottom: -1rem;
}
.about__info::after {
  content: "";
  top: 50%;
  left: 50%;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
  animation: pulse 1s infinite;
}
.navbar__link,
.navbar__links {
  position: absolute !important;
  left: -2rem;
  width: fit-content;
}
.navbar__links {
  flex-direction: column;
  bottom: 3rem;
}
.navbar__link {
  bottom: -2rem;
  right: -2rem;
}
@keyframes pulse {
  30% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0 0 3px 3px magenta;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.3);
    box-shadow: 0 0 3px 0 magenta;
  }
}
@media screen and (max-width: 576px) {
  .about {
    width: 90%;
    font-size: 1rem;
    justify-content: center;
    transform: none;
  }
  .navbar__text {
    font-size: 1.3rem !important;
    line-height: 1.3rem !important;
  }
  .about__text-primary {
    font-size: 1.7rem;
  }
  .about__text-secondary {
    font-size: 1.2rem;
  }
}
