.navbar {
  position: sticky;
  top: 0;
  padding: 20px 10px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 3;
  border-bottom: 1px solid #ddd;
}
.navbar__text,
.navbar__text-h1 {
  line-height: 2rem;
  font-size: 2rem;
  font-weight: bold;
  cursor: pointer;
  margin: 0 10px;
  display: flex;
  align-items: center;
  position: relative;
}
.navbar__text:hover::after {
  position: absolute;
  font-size: 1rem;
  left: -180%;
  top: 20%;
}
.navbar__links--social {
  display: flex;
  flex-direction: column;
  transition: all 1s;
  height: 40vh;
  justify-content: space-around;
}
.navbar__text:nth-child(1):hover::after {
  content: "Github";
}
.navbar__text:nth-child(2):hover::after {
  content: "Youtube";
}
.navbar__text:nth-child(3):hover::after {
  content: "Telegram";
}
.navbar__text:nth-child(4):hover::after {
  content: "Instagram";
}
.navbar__text:nth-child(5):hover::after {
  content: "Leetcode";
}

.navbar__logo::after {
  content: "";
  background: linear-gradient(90deg, magenta, rgba(255, 0, 0, 0.5));
  position: absolute;
  right: 0px;
  width: 50px;
  height: 50px;
  z-index: -1;
  border-radius: 4px;
  transform: rotate(45deg);
  box-shadow: var(--shadow);
}
.navbar__logo:hover::after {
  transform: rotate(0);
  transition: 0.5s all ease-in-out;
  width: 100%;
}
.navbar__links {
  display: flex;
}
.navbar__links-translate {
  animation: translate 500ms ease-in-out;
}
@keyframes translate {
  0% {
    transform: scale(0);
  }
}
.shadow {
  display: flex;
  float: left;
  color: var(--color);
  padding: 10px;
  border-radius: 50px;
  background: var(--background);
  box-shadow: var(--shadow);
  transition: 0.5s;
  cursor: pointer;
}
.shadow:hover {
  box-shadow: var(--shadow-inset);
}
.navbar__toggle {
  background-color: var(--background);
  border: 1px solid #cccccc20;
  padding: 5px;
  border-radius: 20px;
  display: flex;
}
.navbar__toggle-active {
  animation: scale 500ms 1 linear alternate;
}
@keyframes scale {
  50% {
    transform: scale(0);
  }
}
.navbar__toggle {
  transition: all 1s;
}
.navbar__side {
  display: flex;
  flex-direction: row;
  width: 40%;
  justify-content: space-around;
}
.navbar__side--link {
  font-size: 1.1rem;
  text-transform: capitalize;
}
.navbar__side--link:hover {
  color: #0088cc;
}

@media screen and (max-width: 676px) {
  .navbar__link {
    position: absolute;
  }
}
