.admin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}
.admin__file {
  position: relative;
}
.admin__file > input {
  opacity: 0;
  position: relative;
  width: 100px;
  height: 100px;
  z-index: 1;
  cursor: pointer;
}
.admin__file::after {
  position: absolute;
  left: 50%;
  top: calc(0% + 10px);
  transform: translate(-50%, -10%);
  content: "";
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px dashed deeppink;
  z-index: -1;
}
.admin__file::before {
  position: absolute;
  content: "pic";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 1rem;
  color: deeppink;
}
.admin__submit > button {
  outline: none;
  border: 1px solid pink;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: deeppink;
  color: white;
  width: 250px;
  cursor: pointer;
}
.admin__password {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translate(-50%, 0%);
}
.admin__password > input {
  width: 250px;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid pink;
  background-color: white;
  color: deeppink;
  outline: none;
  border: none;
}
