:root {
  --background: #e6e6e6;
  --color: #033333;
  --shadow: 0.4rem 0.4rem 1rem #ccc, -0.4rem -0.4rem 1rem #fff;
  --shadow-inset: 0.4rem 0.4rem 1rem #ccc inset, -0.4rem -0.4rem 1rem #fff inset;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  color: var(--color);
  scroll-behavior: smooth;
}
.dark {
  --shadow: 0.4rem 0.4rem 1rem #022, -0.4rem -0.4rem 1rem #033 !important;
  --shadow-inset: 0.4rem 0.4rem 1rem #022 inset, -0.4rem -0.4rem 1rem #033 inset !important;
  --background: #033333 !important;
  --color: #e6e6e6 !important;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
  overflow-x: hidden;
}
.App {
  text-align: center;
}




#projects {
  margin: 50px 0 25px 0;
}
::-webkit-scrollbar {
  display: none;
}
::-webkit-scrollbar-track{
  background-color: #e6e6e6;
}
::-webkit-scrollbar-thumb{
  background-color: #0088cc30;
}
