.projectsAll {
  padding: 2.5%;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px 5px 0px 5px;
  width: 300px;
  margin: 0 20px;
  scroll-snap-align: none;
}
.project__image {
  position: relative;
  overflow: hidden;
}
.project__image > img {
  max-width: 300px;
  min-height: 250px;
  width: 100%;
  object-fit: cover;
  transition: 0.5s all;
  border-top-left-radius: 50px;
  z-index: 1;
}
.project__image--img:hover {
  transform: scale(1.3);
}
.project__title {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1rem;
  gap: 10px;
  position: absolute;
  bottom: 0;
  backdrop-filter: blur(10px);
  width: 100%;
}
.project__title--main > h3 {
  font-size: 1rem;
  color: #fff;
}
.project__title--sub {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.project__title--sub > a,
.project__body--icon > svg:hover {
  color: #efefef;
  fill: white;
}
.project__body {
  font-size: 16px;
  display: flex;
  flex-direction: column;
  transition: 1s all;
}

.project__body--icon {
  position: absolute;
  font-size: 1rem;
  right: 10px;
  cursor: pointer;
}
.project__body--text {
  color: #111;
  padding: 5px 1px;
  overflow: hidden;
  transition: 1s all;
}
